.div-login{
    position: relative;

    height: 100vh;
}
.section-login{
    display: flex;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 95%;
    max-width: 400px;
    height: 340px;
    box-shadow: 7px 13px 37px #000;
    border-top: 5px solid;
}
.h1-login{
    margin-top: 10px;
    margin-bottom: 25px;
    border-bottom: 1px solid;
}
.div1-login{
    margin: auto;
    text-align: center;
}
.login-div-check-theme{
    margin-left: 85%;
    position: fixed;

}
.login-div1-check-theme select{
    appearance: none;
}
.login-div1-check-theme input{
    visibility: hidden;
    position: absolute;
    right: 0;
}
.login-div1-check-theme{
    position: relative;
    padding:5px 0 5px 60px;
    display: block;
}
.login-div1-check-theme input + i{
    background: #f0f0f0;
    border:2px solid rgba(0,0,0,0.2);
    position: absolute;
}
.login-div1-check-theme input[type=checkbox ] + i{
     width: 88px;
     height: 30px;
     border-radius: 25px;
     font-size: 12px;
 }
.login-div1-check-theme input[type=checkbox] + i:before{
    content: '';
    font-size: 18px;
    padding: 0;
    width: 30px;
    height: 30px;
    background: #fff;
    border-radius: 50%;
    position: absolute;
    z-index: 1;
    left: 0;
    box-shadow: 3px 0 3px 0 rgba(0,0,0,0.2);
    transition: all ease 0.25s;
}
.login-div1-check-theme input[type=checkbox]:checked + i:before{
    left: 57px;
    -webkit-box-shadow: -3px 0 3px 0 rgba(0,0,0,0.2);
    box-shadow: 3px 0 3px 0 rgba(0,0,0,0.2);
    transition: all ease 0.25s;
}
.login-div1-check-theme input[type=checkbox]:checked + i{
     background: #244f3a;
    transition: all 0.25s linear 0.25s;
 }
.login-div1-check-theme input[type=checkbox] + i:after{
    content: 'â˜¾';
    position: absolute;
    font-size: 22px;
    color: rgba(255,255,255,0.6);
    left: 16px;
    opacity: 0;
}
.login-div1-check-theme input[type=checkbox]:checked + i:after{
    opacity: 1;
}
.login-div1-check-theme input[type=checkbox]:checked + i span{
    visibility: hidden;
    display: none;
    transition: all 0.25s ease 0.25s;
}
.login-div1-check-theme input[type=checkbox] + i span{
    display: flex;
    padding-top: 8px;
    padding-left: 32px;
    transition: all 0.25s ease 0.25s;
}